
.titre-home {
grid-column: 1/5;
position : relative;
margin: 75px;
&::after {
  content: '';
  display: block;
  height:20px;
  background-color: $mc--med-1;
  border-radius: 15px;
  width : 235px;
  float: left;
  margin-top: -30px;
  margin-left: -30px;
}
}
.main-grid-equipe {
display: flex;
flex-wrap: wrap;
justify-content: space-around;
flex-direction: row;
}
@media screen and (max-width: 320px){
  .titre-home{
    margin:40px;
  }
}
